<template>
<div :class="{'drawer-opened': openDrawer}">
  <MainMenu title="订单评价管理" />
  <mu-sub-header class="header_list">
    <mu-flat-button :label="item" v-for="item,index in headerList" :key="index"/>
  </mu-sub-header>
  <div class="table_box">
    <mu-list>
      <mu-list-item v-for="item,index in commentItems" :key="index" :title="item.content" :class="[item.iscur?acti:noacti,common]" titleClass="manege_comment_title" @click="changeActive(index)"></mu-list-item>
      <div class="add_new_comment">
        <mu-raised-button label="+新增app展示评价" backgroundColor="#2F4971" @click="openAppShow"/>
        <mu-dialog :open="dialogAppShow" @close="cancelAppShow" dialogClass="dialog_appshow">
          <form class="formStyle" id="addAppShow">
            <div>
              <mu-flexbox>
                <mu-flexbox-item>
                  头像上传
                </mu-flexbox-item>
                <mu-flexbox-item grow="4">
                  <mu-flat-button label="上传头像">
                    <input type="file" class="file-button" ref="headPortrait" accept="image/jpeg,image/jpg,image/png" @change="addHeadPortrait"/>
                  </mu-flat-button>
                </mu-flexbox-item>
              </mu-flexbox>
              <ul>
                <li class="img_upload_left">
                  <img :src="addAppShow.headPortrait"/>
                </li>
              </ul>
              <mu-flexbox>
                <mu-flexbox-item>
                  <span>昵称</span>
                </mu-flexbox-item>
                <mu-flexbox-item grow="4">
                  <mu-text-field hintText="请输入昵称" v-model="addAppShow.nickName"/>
                </mu-flexbox-item>
              </mu-flexbox>
              <mu-flexbox>
                <mu-flexbox-item>
                  <span>评论时间</span>
                </mu-flexbox-item>
                <mu-flexbox-item grow="4">
                  <mu-date-picker container="inline" mode="landscape" hintText="选择日期" v-model="addAppShow.pubTime"/>
                </mu-flexbox-item>
              </mu-flexbox>
              <mu-flexbox>
                <mu-flexbox-item>
                  <span>服务类别</span>
                </mu-flexbox-item>
                <mu-flexbox-item grow="4">
                  <select v-model="addAppShow.serviceType">
                    <option value="" disabled>-请选择-</option>
                    <option>内饰清洗</option>
                    <option>外观清洗</option>
                    <option>五座小轿车</option>
                    <option>七座商务车</option>
                  </select>
                </mu-flexbox-item>
              </mu-flexbox>
              <mu-flexbox>
                <mu-flexbox-item>
                  <span>评论图片</span>
                </mu-flexbox-item>
                <mu-flexbox-item grow="4">
                  <mu-flat-button label="上传图片(最多四张)">
                    <input type="file" class="file-button" ref="commentImg" multiple accept="image/jpeg,image/jpg,image/png" @change="addImgShow"/>
                  </mu-flat-button>
                </mu-flexbox-item>
              </mu-flexbox>
              <ul>
                <li class="img_upload_left" v-for="item,index in addAppShow.imgShow" :key="index">
                  <img :src="item"/>
                  <span class="del_img_show" @click="delImgShow(index)">x</span>
                </li>
              </ul>
              <mu-flexbox>
                <mu-flexbox-item>
                  <span>评星</span>
                </mu-flexbox-item>
                <mu-flexbox-item grow="4">
                  <input type="number" min="0" max="5" v-model="addAppShow.star"/>
                </mu-flexbox-item>
              </mu-flexbox>
              <div>
                <p>评论内容</p>
                <textarea name="" id="" cols="42" rows="5" placeholder="200字以内" v-model="addAppShow.content"></textarea>
              </div>
            </div>
          </form>
          <mu-flat-button slot="actions" @click="cancelAppShow" label="取消"/>
          <mu-flat-button slot="actions" @click="confirmAppShow" label="确定"/>
        </mu-dialog>
      </div>
      <div class="good_reputation">
        <mu-raised-button :label="'好评度'+praiseDegree+'%'" backgroundColor="#2F4971" @click="openReputation"/>
        <mu-dialog :open="dialogReputation" @close="cancelReputation" dialogClass="dialog_reputation">
          好评度&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <mu-text-field hintText="两位小数数字" ref="praiseInput" :errorText="errTxt"/>
          <mu-flat-button slot="actions" @click="cancelReputation" label="取消"/>
          <mu-flat-button slot="actions" @click="confirmReputation" label="确定"/>
        </mu-dialog>
      </div>
    </mu-list>
    <div class="hr"></div>
    <div class="table_txt_center">
      <mu-table multiSelectable enableSelectAll showCheckbox v-show="!toggleTable" @rowSelection="clickTableRow">
        <mu-thead slot="header">
          <mu-tr>
            <mu-th tooltip="序号">序号</mu-th>
            <mu-th tooltip="评价内容">评价内容</mu-th>
            <mu-th tooltip="用户昵称">用户昵称</mu-th>
            <mu-th tooltip="评价时间">评价时间</mu-th>
            <mu-th tooltip="图片数">图片数</mu-th>
            <mu-th tooltip="评星">评星</mu-th>
            <mu-th tooltip="app展示">app展示</mu-th>
            <mu-th tooltip="操作">操作</mu-th>
            <mu-th tooltip="是否置顶">是否置顶</mu-th>
          </mu-tr>
        </mu-thead>
        <mu-tbody>
          <mu-tr v-for="item,index in tableData" :key="index" :selected="item.isSelected">
            <mu-td>{{index + 1}}</mu-td>
            <mu-td>{{item.content}}</mu-td>
            <mu-td>{{item.nickName}}</mu-td>
            <mu-td>{{item.pubTime}}</mu-td>
            <mu-td>{{item.imgCount.length}}</mu-td>
            <mu-td>{{item.star}}</mu-td>
            <mu-td :class="[item.appShow==='是'&&appShow]">{{item.appShow}}</mu-td>
            <mu-td>
              <mu-raised-button label="详情" @click.stop="openDetail(index)" backgroundColor="#169BD5" class="detail_button_size"/>
            </mu-td>
            <mu-td>
              <mu-raised-button label="置顶" @click.stop="isTop(index)" backgroundColor="#169BD5" class="detail_button_size"></mu-raised-button>
            </mu-td>
          </mu-tr>
        </mu-tbody>
      </mu-table>
      <mu-table multiSelectable enableSelectAll v-show="toggleTable" @rowSelection="clickAppRow">
        <mu-thead slot="header">
          <mu-tr>
            <mu-th tooltip="序号">序号</mu-th>
            <mu-th tooltip="评价内容">评价内容</mu-th>
            <mu-th tooltip="用户昵称">用户昵称</mu-th>
            <mu-th tooltip="评价时间">评价时间</mu-th>
            <mu-th tooltip="图片数">图片数</mu-th>
            <mu-th tooltip="评星">评星</mu-th>
            <mu-th tooltip="操作">操作</mu-th>
            <mu-th tooltip="是否置顶">是否置顶</mu-th>
          </mu-tr>
        </mu-thead>
        <mu-tbody>
          <mu-tr v-for="item,index in appData" :key="index" :selected="item.isSelected">
            <mu-td>{{index + 1}}</mu-td>
            <mu-td>{{item.content}}</mu-td>
            <mu-td>{{item.nickName}}</mu-td>
            <mu-td>{{item.pubTime}}</mu-td>
            <mu-td>{{item.imgCount.length}}</mu-td>
            <mu-td>{{item.star}}</mu-td>
            <mu-td>
              <mu-raised-button label="编辑" backgroundColor="#169BD5" class="detail_button_size" @click.stop="editAppShow(index)"></mu-raised-button>
            </mu-td>
            <mu-td>
              <mu-raised-button label="置顶" @click.stop="appToTop(index)" backgroundColor="#169BD5" class="detail_button_size"></mu-raised-button>
            </mu-td>
          </mu-tr>
        </mu-tbody>
      </mu-table>
      </div>
      <div class="table_foot_info">
        <mu-raised-button v-if="!toggleTable" label="反选" backgroundColor="#2F4971" class="button_size" @click="reverseTableSelect"/>
        <mu-raised-button v-if="toggleTable" label="反选" backgroundColor="#2F4971" class="button_size" @click="reverseAppSelect"/>
        <mu-raised-button v-if="!toggleTable" label="批量加入展示" backgroundColor="#2F4971" class="button_size" @click="batchToShow"/>
        <mu-raised-button v-if="toggleTable" label="批量移除展示" backgroundColor="#2F4971" class="button_size" @click="batchFromShow"/>
        <mu-pagination :total="total" :current="current" @pageChange="changePage" class="pagination_position"></mu-pagination>
      </div>
  </div>
  <mu-dialog :open="dialogDetail" @close="closeDetail" dialogClass="dialog_detail">
    <mu-list>
      <mu-list-item :title="oneTableData.nickName" titleClass="user_name" disabled>
        <mu-avatar slot="left" :src="oneTableData.headPortrait"/>
        <mu-avatar slot="right" :src="oneTableData.headPortrait"/>
      </mu-list-item>
    </mu-list>
    <mu-grid-list>
      <mu-sub-header>{{oneTableData.content}}</mu-sub-header>
      <ul class="img_box">
        <li class="img_left" v-for="item,index in oneTableData.imgCount" :key="index">
          <img :src="item"/>
        </li>
      </ul>
    </mu-grid-list>
    <span class="service_time">{{oneTableData.pubTime}}</span>
    <span class="service_info">{{oneTableData.serviceType}}</span>
    <mu-flat-button slot="actions" @click="closeDetail" label="取消"/>
    <mu-flat-button slot="actions" @click="closeDetail" label="确定"/>
  </mu-dialog>
  <mu-dialog :open="editAppDialog" @close="cancelEditApp" dialogClass="dialog_appshow">
    <form class="formStyle">
      <div>
        <mu-flexbox>
          <mu-flexbox-item>
            头像上传
          </mu-flexbox-item>
          <mu-flexbox-item grow="4">
            <mu-flat-button label="上传头像">
              <input type="file" class="file-button" ref="editheadPortrait" accept="image/jpeg,image/jpg,image/png" @change="editHeadPortrait"/>
            </mu-flat-button>
          </mu-flexbox-item>
        </mu-flexbox>
        <ul>
          <li class="img_upload_left">
            <img :src="oneAppData.headPortrait"/>
          </li>
        </ul>
        <mu-flexbox>
          <mu-flexbox-item>
            <span>昵称</span>
          </mu-flexbox-item>
          <mu-flexbox-item grow="4">
            <mu-text-field hintText="请输入昵称" v-model="oneAppData.nickName"/>
          </mu-flexbox-item>
        </mu-flexbox>
        <mu-flexbox>
          <mu-flexbox-item>
            <span>评论时间</span>
          </mu-flexbox-item>
          <mu-flexbox-item grow="4">
            <mu-date-picker container="inline" mode="landscape" hintText="选择日期" v-model="oneAppData.pubTime"/>
          </mu-flexbox-item>
        </mu-flexbox>
        <mu-flexbox>
          <mu-flexbox-item>
            <span>服务类别</span>
          </mu-flexbox-item>
          <mu-flexbox-item grow="4">
            <select v-model="oneAppData.serviceType">
              <option value="" disabled>-请选择-</option>
              <option>内饰清洗</option>
              <option>外观清洗</option>
              <option>五座小轿车</option>
              <option>七座商务车</option>
            </select>
          </mu-flexbox-item>
        </mu-flexbox>
        <mu-flexbox>
          <mu-flexbox-item>
            <span>评论图片</span>
          </mu-flexbox-item>
          <mu-flexbox-item grow="4">
            <mu-flat-button label="上传图片(最多四张)">
              <input type="file" class="file-button" ref="editcommentImg" multiple accept="image/jpeg,image/jpg,image/png" @change="editAddImgShow"/>
            </mu-flat-button>
          </mu-flexbox-item>
        </mu-flexbox>
        <ul>
          <li class="img_upload_left" v-for="item,idx in oneAppData.imgCount" :key="idx">
            <img :src="item"/>
            <span class="del_img_show" @click="editDelImgShow(idx)">x</span>
          </li>
        </ul>
        <mu-flexbox>
          <mu-flexbox-item>
            <span>评星</span>
          </mu-flexbox-item>
          <mu-flexbox-item grow="4">
            <input type="number" min="0" max="5" v-model="oneAppData.star"/>
          </mu-flexbox-item>
        </mu-flexbox>
        <div>
          <p>评论内容</p>
          <textarea name="" id="" cols="42" rows="5" placeholder="200字以内" v-model="oneAppData.content"></textarea>
        </div>
      </div>
    </form>
    <mu-flat-button slot="actions" @click="cancelEditApp" label="取消"/>
    <mu-flat-button slot="actions" @click="confirmEditApp" label="确定"/>
  </mu-dialog>
  <mu-popup position="top" :overlay="false" :open="topPopup1">
    您还有信息未填完！
  </mu-popup>
  <mu-popup position="top" :overlay="false" :open="topPopup2">
    图片不能超过4张！
  </mu-popup>
</div>
</template>

<script>
import Vue from 'vue'
import { mapState } from 'vuex'
import MainMenu from '../units/mainMenu'
import flatButton from 'muse-components/flatButton'
import raisedButton from 'muse-components/raisedButton'
import subHeader from 'muse-components/subHeader'
import dialog from 'muse-components/dialog'
import datePicker from 'muse-components/datePicker'
import { gridList } from 'muse-components/gridList'
import Popup from 'muse-components/popup'

Vue.component(flatButton.name, flatButton)
Vue.component(raisedButton.name, raisedButton)
Vue.component(subHeader.name, subHeader)
Vue.component(dialog.name, dialog)
Vue.component(datePicker.name, datePicker)
Vue.component(gridList.name, gridList)
Vue.component(Popup.name, Popup)
export default {
  name: 'OrderManegement',
  components: {
    MainMenu
  },
  data () {
    return {
      headerList: ['上门洗车', '加玻璃水', '清洗机舱', '玻璃镀膜', '加冷却液', '车身打蜡', '轮胎补气', '空调清洗', '内饰清洗', '轮辋清洗'],
      tableData: [{
        headPortrait: '/static/location.png',
        content: '真不错，特别好',
        nickName: 'AA',
        pubTime: '2017-07-17',
        imgCount: ['/static/car.png', '/static/car.png', '/static/car.png', '/static/car.png'],
        serviceType: '内饰清洗',
        star: 5,
        appShow: '否',
        isSelected: false
      }, {
        headPortrait: '/static/people.png',
        content: '真不错，特别好',
        nickName: 'BB',
        pubTime: '2017-07-17',
        imgCount: ['/static/car.png', '/static/car.png', '/static/car.png', '/static/car.png'],
        serviceType: '内饰清洗',
        star: 5,
        appShow: '否',
        isSelected: false
      }, {
        headPortrait: '/static/people.png',
        content: '真不错，特别好',
        nickName: 'CC',
        pubTime: '2017-07-17',
        imgCount: ['/static/car.png', '/static/car.png', '/static/car.png', '/static/car.png'],
        serviceType: '内饰清洗',
        star: 5,
        appShow: '否',
        isSelected: false
      }, {
        headPortrait: '/static/people.png',
        content: '真不错，特别好',
        nickName: 'DD',
        pubTime: '2017-07-17',
        imgCount: ['/static/car.png', '/static/car.png', '/static/car.png', '/static/car.png'],
        serviceType: '内饰清洗',
        star: 5,
        appShow: '否',
        isSelected: false
      }, {
        headPortrait: '/static/people.png',
        content: '真不错，特别好',
        nickName: 'EE',
        pubTime: '2017-07-17',
        imgCount: ['/static/car.png', '/static/car.png', '/static/car.png', '/static/car.png'],
        serviceType: '内饰清洗',
        star: 5,
        appShow: '否',
        isSelected: false
      }
      ],
      appData: [{
        headPortrait: '',
        content: '真不错，特别好',
        nickName: 'app1',
        pubTime: '2017-07-17',
        serviceType: '',
        imgCount: ['/static/car.png', '/static/car.png', '/static/car.png', '/static/car.png'],
        star: 5,
        appShow: '否',
        isSelected: false
      }, {
        headPortrait: '',
        content: '真不错，特别好',
        nickName: 'app2',
        pubTime: '2017-07-17',
        serviceType: '',
        imgCount: ['/static/car.png', '/static/car.png', '/static/car.png', '/static/car.png'],
        star: 5,
        appShow: '否',
        isSelected: false
      }
      ],
      contentList: [],
      commentItems: [{
        content: '用户评价',
        iscur: true
      }, {
        content: 'app展示评价',
        iscur: false
      }
      ],
      acti: 'comment_item_active',
      noacti: 'comment_item_noactive',
      common: 'user_comment',
      total: 50,
      current: 1,
      appShow: 'app_show',
      toggleTable: false,
      dialogAppShow: false,
      dialogReputation: false,
      dialogDetail: false,
      editAppDialog: false,
      praiseDegree: '100',
      errTxt: '',
      addAppShow: {
        headPortrait: '',
        nickName: '',
        pubTime: '',
        serviceType: '',
        imgShow: [],
        star: '',
        content: ''
      },
      oneTableData: {},
      oneAppData: {},
      topPopup1: false,
      topPopup2: false
    }
  },
  computed: {
    ...mapState([
      'openDrawer'
    ])
  },
  watch: {
    topPopup1 (val) {
      if (val) {
        setTimeout(() => {
          this.topPopup1 = false
        }, 2000)
      }
    },
    topPopup2 (val) {
      if (val) {
        setTimeout(() => {
          this.topPopup2 = false
        }, 2000)
      }
    }
  },
  methods: {
    changeActive: function (index) {
      var _this = this
      this.commentItems.map(function (v, i) {
        i === index ? v.iscur = true : v.iscur = false
        i === index ? _this.toggleTable = true : _this.toggleTable = false
      })
    },
    changePage: function (newIndex) {
    },
    clickTableRow: function (index) {
      console.log(index)
      for (var r = 0; r < this.tableData.length; r++) {
        this.tableData[r].isSelected = false
      }
      for (var i = 0; i < index.length; i++) {
        this.tableData[index[i]].isSelected = true
        console.log(this.tableData[index[i]].isSelected)
      }
    },
    clickAppRow: function (index) {
      for (var r = 0; r < this.appData.length; r++) {
        this.appData[r].isSelected = false
      }
      for (var i = 0; i < index.length; i++) {
        this.appData[index[i]].isSelected = true
        console.log(this.appData[index[i]].isSelected)
      }
    },
    batchToShow: function () {
      var _this = this
      for (var i = 0; i < _this.tableData.length; i++) {
        if (_this.tableData[i].isSelected) {
          _this.tableData[i].appShow = '是'
          _this.appData.push(_this.tableData[i])
        }
      }
    },
    batchFromShow: function () {
      // 删除appData中选中的row
      var indexs = this.appData.map((n, i) => n.isSelected ? i : -1).filter(i => i >= 0)
      indexs = indexs.reverse()
      for (var i = 0; i < indexs.length; i++) {
        var droped = this.appData.splice(indexs[i], 1)
        // 将tableData中对应的appShow置为'否'
        this.tableData.indexOf(droped[0]) !== -1 && (this.tableData[this.tableData.indexOf(droped[0])].appShow = '否')
      }
    },
    reverseTableSelect: function () {
      for (var i = 0; i < this.tableData.length; i++) {
        this.tableData[i].isSelected = !this.tableData[i].isSelected
        console.log(this.tableData[i].isSelected)
      }
    },
    reverseAppSelect: function () {
      for (var i = 0; i < this.appData.length; i++) {
        this.appData[i].isSelected = !this.appData[i].isSelected
      }
    },
    isTop: function (index) {
      var i = this.tableData.splice(index, 1)
      this.tableData.unshift(i[0])
    },
    appToTop: function (index) {
      var i = this.appData.splice(index, 1)
      this.appData.unshift(i[0])
    },
    openDetail: function (index) {
      this.dialogDetail = true
      this.oneTableData = this.tableData[index]
    },
    closeDetail: function () {
      this.dialogDetail = false
    },
    openAppShow: function () {
      this.dialogAppShow = true
    },
    confirmAppShow: function () {
      console.log(this.addAppShow.serviceType)
      var app = this.addAppShow
      if (app.headPortrait === '' ||
          app.content === '' ||
          app.nickName === '' ||
          app.pubTime === '' ||
          app.serviceType === '' ||
          app.imgShow === [] ||
          app.star === ''
      ) {
        this.topPopup1 = true
      } else if (app.imgShow.length > 4) {
        this.topPopup2 = true
      } else {
        this.appData.push({
          headPortrait: this.addAppShow.headPortrait,
          content: this.addAppShow.content,
          nickName: this.addAppShow.nickName,
          pubTime: this.addAppShow.pubTime,
          serviceType: this.addAppShow.serviceType,
          imgCount: this.addAppShow.imgShow,
          star: this.addAppShow.star,
          appShow: '否',
          isSelected: false
        })
        this.addAppShow.headPortrait = ''
        this.addAppShow.content = ''
        this.addAppShow.nickName = ''
        this.addAppShow.pubTime = ''
        this.addAppShow.serviceType = ''
        this.addAppShow.imgShow = []
        this.addAppShow.star = ''
        this.dialogAppShow = false
      }
    },
    addHeadPortrait: function () {
      var headImg = this.$refs.headPortrait.files[0]
      this.addAppShow.headPortrait = '/static/' + headImg['name']
      console.log(this.addAppShow.headPortrait)
    },
    editHeadPortrait: function () {
      var headImg = this.$refs.editheadPortrait.files[0]
      this.oneAppData.headPortrait = '/static/' + headImg['name']
      console.log(this.oneAppData.headPortrait)
    },
    addImgShow: function () {
      var len = this.$refs.commentImg.files
      for (var i = 0; i < len.length; i++) {
        this.addAppShow.imgShow.push('/static/' + len[i].name)
      }
      console.log(this.addAppShow.imgShow)
    },
    delImgShow: function (index) {
      this.addAppShow.imgShow.splice(index, 1)
    },
    cancelAppShow: function () {
      this.dialogAppShow = false
    },
    openReputation: function () {
      this.errTxt = ''
      this.dialogReputation = true
    },
    confirmReputation: function () {
      var praiseCurDegree = this.$refs.praiseInput.$refs.input.value
      praiseCurDegree === '' && (this.dialogReputation = false)
      var reg = /^(\d|[1-9]\d|100)(\.\d{1,2})?$/
      if (reg.test(praiseCurDegree)) {
        this.praiseDegree = praiseCurDegree
        this.errTxt = ''
        this.dialogReputation = false
      } else {
        this.errTxt = '输入格式有误'
      }
    },
    cancelReputation: function () {
      this.dialogReputation = false
    },
    editAppShow: function (index) {
      this.editAppDialog = true
      this.oneAppData = this.appData[index]
      console.log(this.oneAppData)
    },
    editAddImgShow: function () {
      console.log(this.oneAppData.imgCount)
      var len = this.$refs.editcommentImg.files
      for (var i = 0; i < len.length; i++) {
        this.oneAppData.imgCount.push('/static/' + len[i].name)
      }
    },
    editDelImgShow: function (index) {
      this.oneAppData.imgCount.splice(index, 1)
    },
    cancelEditApp: function () {
      this.editAppDialog = false
    },
    confirmEditApp: function () {
      var app = this.oneAppData
      if (app.headPortrait === '' ||
          app.content === '' ||
          app.nickName === '' ||
          app.pubTime === '' ||
          app.serviceType === '' ||
          app.imgCount === [] ||
          app.star === ''
      ) {
        this.topPopup1 = true
      } else if (app.imgCount.length > 4) {
        this.topPopup2 = true
      } else {
        this.editAppDialog = false
      }
    }
  }
}
</script>

<style scoped>
