  }
  .table_foot_info{
    margin:20px;
  }
  .button_size{
    height:2.2rem;
  }
  .add_new_comment{
    float:left;
    margin-left:3rem;
    margin-top:0.8rem;
  }
  .good_reputation{
    float:left;
    margin-left:5rem;
    margin-top:0.8rem;
  }
  .detail_button_size{
    height:2rem;
    min-width:60px;
  }
  .pagination_position{
    float:right;
  }
  .app_show{
    background-color:#0c0;
  }
  .file-button{
    position: absolute;
    left: 0;
