  }
  .img_upload_left{
    float:left;
    margin-right:10px;
    position:relative;
  }
  .del_img_show{
    display:inline-block;
    width:20px;
    height:20px;
    line-height:18px;
    border-radius:50%;
    text-align:center;
    background-color:#111;
    color:#fff;
    cursor:pointer;
    position:absolute;
    top:-10px;
    right:-10px;
  }
  .service_time{
    margin-left:15px;
    font-size:14px;
  }
  .service_info{
    float:right;
    margin-right:15px;
    font-size:14px;
  }
</style>
<style>
  .manege_comment_title{
      color:#fff;
  }
  .table_txt_center .mu-td,.table_txt_center .mu-th{
      text-align:center;
   }
  .dialog_appshow{
    width:410px !important;
    overflow:auto;
  }
  .dialog_reputation{
    width:400px !important;
    overflow:auto;
  }
  .dialog_detail{
    width:440px !important;
